import React from "react";

class Covid extends React.Component {
  constructor() {
    super();
    this.state = {
      usData: "",
      stateData: [],
    };
  }

  componentDidMount() {
    fetch("https://api.covidtracking.com/v1/us/current.json")
      .then((resp) => resp.json())
      .then((usResp) => {
        this.setState({ usData: usResp[0] });
      });

    fetch("https://api.covidtracking.com/v1/states/current.json")
      .then((resp) => resp.json())
      .then((findResp) => {
        this.setState({ stateData: findResp });
      });
  }

  render() {
    const { usData, stateData } = this.state;
    var lastMod =
      usData.lastModified == null ? "" : usData.lastModified.split(".")[0];

    return (
      <div
        class="container"
        style={{
          textAlign: "center",
        }}
      >
        <div
          style={{
            height: "100px",
            backgroundColor: "lightslategray",
          }}
        >
          <h1>CORONA Virus in the United States</h1>
          <p>Last synced from covidtracking {lastMod} by Qunli</p>
        </div>
        <br />
        <table class="table">
          <tbody>
            <tr>
              <th>New Cases</th>
              <th>Positive Cases</th>
              <th>Negative Cases</th>
              <th>Deaths</th>
              <th>Total tested</th>
            </tr>
            <tr>
              <td>{usData.totalTestResultsIncrease}</td>
              <td>{usData.positive}</td>
              <td>{usData.negative}</td>
              <td>{usData.death}</td>
              <td>{usData.totalTestResults}</td>
            </tr>
          </tbody>
        </table>
        <br />
        <h3
          style={{
            height: "40px",
            backgroundColor: "lightslategray",
          }}
        >
          CORONA Virus tracker by State
        </h3>
        <table class="table table-striped table-sm">
          <tbody>
            <tr>
              <th>State</th>
              <th>Positive Cases</th>
              <th>Negative Cases</th>
              <th>Deaths</th>
              <th>Total tested</th>
            </tr>
            {stateData.map((item) => (
              <tr key={item.fips}>
                <td>{item.state}</td>
                <td>{item.positive}</td>
                <td>{item.negative}</td>
                <td>{item.death}</td>
                <td>{item.totalTestResults}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  }
}

export default Covid;
